.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 5px;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 1px 1px 1px black;
  background: rgb(167 203 204);
}

.formNameNumber {
  margin: 5px;
}

.formButton {
  width: 100px;
  height: 30px;

  margin-top: 10px;
  font-size: 12px;
  font-weight: 500;

  border-radius: 5px;
  transition-duration: 0.1s;
  box-shadow: 1px 1px 11px 1px #797a7c;
}

.formButton:active {
  transform: scale(1.1);
  background: #aafcea;
  color: #062afa;
  box-shadow: 1px 1px 11px 1px #062afa;
  transition-duration: 0.1s;
}
